<template>
<!--我的->增票管理-->
<div class="background">
  <Header>
    <div slot="left" class="backIcon"><img :src="`${$cdnImageUrl}/common/back_white.png`"></div>
    <div slot="title">编辑增票信息</div>
    <div slot="right" @click="save(info.isDefault)">保存</div>
  </Header>
  <van-cell-group scrollbar-y="false" style="margin-top: 8.5px;">
    <van-cell class="border-line-placeholder padding-10" style="text-indent: 16px;">
        <span>公司名称 </span>
        <input id='name' placeholder="请填写公司名称" class="border-none width-100" v-model="info.invoiceTitle" style="overflow: scroll;padding-left: 54px;max-width: 260px;">
    </van-cell>
    <van-cell class="border-line-placeholder padding-10" style="text-indent: 16px;">
        <span class="">纳税人识别号 </span>
        <input class="border-none width-100" placeholder="长度为15位或18位或20位" v-model="info.taxPayerNumber" style="padding-left:28px;">
    </van-cell>
    <van-cell class="border-line-placeholder padding-10" style="text-indent: 16px;">
        <span class="">注册地址</span>
        <input class="border-none width-100" placeholder="注册地址" v-model="info.registerAddress" style="overflow: scroll;padding-left: 54px;max-width: 260px;">
    </van-cell>
    <van-cell class="border-line-placeholder padding-10" style="text-indent: 16px;">
        <span class="">注册电话</span>
        <input class="border-none width-100" placeholder="座机(区号-号码)或手机" v-model="info.registerPhone" style="padding-left:54px;">
    </van-cell>
    <van-cell class="border-line-placeholder padding-10" style="text-indent: 16px;">
        <span class="">开户银行</span>
        <input class="border-none width-100" placeholder="请填写开户银行" v-model="info.bankName" style="overflow: scroll;padding-left: 54px;max-width: 260px;">
    </van-cell>
    <van-cell class=" padding-10" style="text-indent: 16px;border: 0;">
        <span class="">开户账号</span>
        <input class="border-none width-100" placeholder="请填写开户账号" v-model="info.bankCardNumber" style="padding-left:54px;">
    </van-cell>
  </van-cell-group>
  <div style="width:100%;height:90px;position: fixed;bottom:0px;background: #f4f4f4;max-width: 7.5rem" @click="save(1)">
    <div style="width:91%;height:44px;margin:23px auto;background: #2979FF;border-radius: 50px;line-height:44px;text-align: center;color: #ffffff;font-size: 17px;">保存设为默认增票信息</div>
  </div>
</div>
</template>

<script>
import Header from '@/components/header'
import { getInvoiceInfoById, updateInvoice, createInvoice } from '@/http/service/goods'
export default {
  components: {
    Header
  },
  data() {
    return {
      info: {
        invoiceTitle: '',
        taxPayerNumber: '',
        registerAddress: '',
        registerPhone: '',
        bankName: '',
        bankCardNumber: '',
        isDefault: 0
      }
    }
  },
  created() {
    if (this.$route.query.id) {
      this.getInvoiceInfoById()
    }
  },
  methods: {
    getInvoiceInfoById() {
      getInvoiceInfoById({ i: this.$route.query.id }).then(res => {
        if (res.success) {
          this.info = res.data
        } else {
          this.$toast.fail(res.message)
        }
        console.log('1234', res)
      })
    },
    save(isDefault) {
      this.$toast.loading({
        message: '加载中...',
        duration: 0,
        forbidClick: true
      })

      if (!(/^.[^&<>]{1,}$/.test(this.info.invoiceTitle)) || (!this.info.invoiceTitle)) {
        this.$toast.fail('公司名称必填,且不能包含特殊字符');
        return;
      }
      if (!(/^[0-9A-Za-z]{18}$|^[0-9A-Za-z]{15}$|^[0-9A-Za-z]{20}$/.test(this.info.taxPayerNumber))) {
        this.$toast.fail('识别号为15位或18位或20位的数字或字母');
        return;
      }
      if (!(/^.[^&<>]{4,}$/.test(this.info.registerAddress)) || (!this.info.registerAddress)) {
        this.$toast.fail('注册地址至少5个字符，且不能包含特殊字符');
        return;
      }
      if ((this.info.registerPhone && this.info.registerPhone.length < 8) || (!this.info.registerPhone)) {
        this.$toast.fail('座机(区号-号码)或手机');
        return;
      }
      if (!this.info.bankName) {
        this.$toast.fail('开户银行为必填，且不能包含特殊字符');
        return;
      }
      if (!this.info.bankCardNumber) {
        this.$toast.fail('开户账号为必填，且不能包含特殊字符');
        return;
      }
      var params = {
        'bankCardNumber': this.info.bankCardNumber,
        'bankName': this.info.bankName,
        'invoiceTitle': this.info.invoiceTitle,
        'isDefault': isDefault,
        'registerAddress': this.info.registerAddress,
        'registerPhone': this.info.registerPhone,
        'taxPayerNumber': this.info.taxPayerNumber
      };
      if (this.$route.query.id) {
        params.id = this.$route.query.id
        updateInvoice(params).then(res => {
          if (res.success) {
            this.$toast.success('保存成功')
            this.$router.go(-1)
          } else {
            this.$toast.success(res.message)
          }
        })
      } else {
        createInvoice(params).then(res => {
          if (res.success) {
            this.$toast.success('保存成功')
            this.$router.go(-1)
          } else {
            this.$toast.success(res.message)
          }
        })
      }
    }
  }
}
</script>

